<template>
  <div class="main-wrapper">
    <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
      <a class="navbar-brand js-scroll-trigger" href="#page-top">
        <span class="d-block d-lg-none">{{ jsonResume.basics.name }}</span>
        <span class="d-none d-lg-block">
          <img class="img-fluid img-profile rounded-circle mx-auto mb-2" src="static/photo.jpg" alt />
        </span>
      </a>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav">
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#about">About</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#work">Work</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#education">Education</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#skills">Skills</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#languages">Languages</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#interests">Interests</a>
          </li>
        </ul>
      </div>
    </nav>

    <div class="container-fluid p-0" id="page-top">
      <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="about">
        <div class="w-100">
          <h1 class="mb-0">
            {{ jsonResume.basics.name }}
            <span class="text-primary">{{ jsonResume.basics.location.region }}</span>
          </h1>
          <div class="subheading mb-5">
            {{ jsonResume.basics.label }}  
            <a v-bind:href="'mailto:' + jsonResume.basics.email"
            >{{ jsonResume.basics.email }}</a>
          </div>
          <p
            class="lead mb-5"
          >{{ jsonResume.basics.summary }}</p>
          <div class="social-icons">
            <a v-bind:href="jsonResume.basics.profiles[0].url">
              <i class="fab fa-linkedin-in"></i>
            </a>
            <a v-bind:href="jsonResume.basics.profiles[1].url">
              <i class="fab fa-github"></i>
            </a>
            <a v-bind:href="jsonResume.basics.profiles[2].url">
              <i class="fab fa-medium"></i>
            </a>
          </div>
        </div>
      </section>

      <hr class="m-0" />

      <section class="resume-section p-3 p-lg-5 d-flex justify-content-center" id="work">
        <div class="w-100">
          <h2 class="mb-5">Work</h2>

          <div
            v-for="workExperience in jsonResume.work"
            v-bind:key="workExperience.name + workExperience.position"
            class="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div class="resume-content">
              <h3 class="mb-0">{{ workExperience.position }}</h3>
              <div class="subheading mb-3">{{ workExperience.name }}</div>
              <p>{{ workExperience.summary }}</p>
            </div>
            <div class="resume-date text-md-right">
              <span v-if="workExperience.endDate" class="text-primary">{{ workExperience.startDate }} to {{ workExperience.endDate }}</span>
              <span v-else class="text-primary">{{ workExperience.startDate }} to Present</span>
            </div>
          </div>

        </div>
      </section>

      <hr class="m-0" />

      <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="education">
        <div class="w-100">
          <h2 class="mb-5">Education</h2>

          <div
            v-for="educationExperience in jsonResume.education"
            v-bind:key="educationExperience.area"
            class="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div class="resume-content">
              <h3 class="mb-0">{{ educationExperience.institution }}</h3>
              <h4>{{ educationExperience.studyType }} || {{ educationExperience.area }}</h4>
              <div>Details:</div>
              <ul>
                <li>
                  GPA: {{ educationExperience.gpa }}
                </li>
                <li>
                  Contents: {{ educationExperience.courses }}
                </li>
              </ul>
            </div>
            <div class="resume-date text-md-right">
              <span class="text-primary">{{ educationExperience.startDate }} - {{ educationExperience.endDate }}</span>
            </div>
          </div>

        </div>
      </section>

      <hr class="m-0" />

      <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="skills">
        <div class="w-100">
          <h2 class="mb-5">Skills</h2>

          <div class="subheading mb-3">Programming Languages &amp; Tools</div>
          <ul class="list-inline dev-icons">
            <li v-for="skill in jsonResume.skills" v-bind:key="skill.name" class="list-inline-item">
              <i v-bind:class="['fab', 'fa-' + skill.name.toLowerCase().trim()]"></i>
            </li>
          </ul>
        </div>
      </section>

      <hr class="m-0" />

      <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="languages">
        <div class="w-100">
          <h2 class="mb-5">Languages</h2>
          <ul class="fa-ul mb-o">
            <li v-for="language in jsonResume.languages" v-bind:key="language.language">
              <i class="fa-li fa fa-language text-warning"></i>
              {{ language.language }} | {{ language.fluency }}
            </li>
          </ul>
        </div>
      </section>

      <hr class="m-0" />

      <section class="resume-section p-3 p-lg-5 d-flex align-items-center" id="interests">
        <div class="w-100">
          <h2 class="mb-5">Interests</h2>
          <ul class="fa-ul mb-0">
            <li v-for="interest in jsonResume.interests" v-bind:key="interest.name">
              <i class="fa-li fa fa-trophy text-warning"></i>
              {{ interest.name }} | {{ interest.keywords }}
            </li>
          </ul>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import jsonResume from './resume.json'
export default {
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })

      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })

      $('body').scrollspy({
        target: '#sideNav'
      })
    })
  },
  data () {
    return {
      title: 'Lus Nbrega | Welcome to my Portfolio',
      jsonResume
    }
  }
}
</script>

<style>
/*!
 * Start Bootstrap - Resume v5.0.7 (https://startbootstrap.com/template-overviews/resume)
 * Copyright 2013-2019 Start Bootstrap
 * Licensed under MIT (https://github.com/BlackrockDigital/startbootstrap-resume/blob/master/LICENSE)
 */

body {
  font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji";
  padding-top: 54px;
  color: #868e96;
}

@media (min-width: 992px) {
  body {
    padding-top: 0;
    padding-left: 17rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Saira Extra Condensed", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 700;
  text-transform: uppercase;
  color: #343a40;
}

h1 {
  font-size: 6rem;
  line-height: 5.5rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2rem;
}

p.lead {
  font-size: 1.15rem;
  font-weight: 400;
}

.subheading {
  text-transform: uppercase;
  font-weight: 500;
  font-family: "Saira Extra Condensed", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.5rem;
}

.social-icons a {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.5rem;
  margin-right: 1rem;
}

.social-icons a:last-child {
  margin-right: 0;
}

.social-icons a:hover {
  background-color: #3a38bd;
}

.dev-icons {
  font-size: 3rem;
}

.dev-icons .list-inline-item i:hover {
  color: #3a38bd;
}

#sideNav .navbar-nav .nav-item .nav-link {
  font-weight: 800;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

#sideNav .navbar-toggler:focus {
  outline-color: #6e92d4;
}

@media (min-width: 992px) {
  #sideNav {
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 17rem;
    height: 100vh;
  }
  #sideNav .navbar-brand {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: auto auto 0;
    padding: 0.5rem;
  }
  #sideNav .navbar-brand .img-profile {
    max-width: 10rem;
    max-height: 10rem;
    border: 0.5rem solid rgba(255, 255, 255, 0.2);
  }
  #sideNav .navbar-collapse {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-flex: 0;
    -ms-flex-positive: 0;
    flex-grow: 0;
    width: 100%;
    margin-bottom: auto;
  }
  #sideNav .navbar-collapse .navbar-nav {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
  }
  #sideNav .navbar-collapse .navbar-nav .nav-item {
    display: block;
  }
  #sideNav .navbar-collapse .navbar-nav .nav-item .nav-link {
    display: block;
  }
}

section.resume-section {
  padding-top: 5rem !important;
  padding-bottom: 5rem !important;
  max-width: 75rem;
}

section.resume-section .resume-item .resume-date {
  min-width: none;
}

@media (min-width: 768px) {
  section.resume-section {
    min-height: 100vh;
  }
  section.resume-section .resume-item .resume-date {
    min-width: 18rem;
  }
}

@media (min-width: 992px) {
  section.resume-section {
    padding-top: 3rem !important;
    padding-bottom: 3rem !important;
  }
}

.bg-primary {
  background-color: #3a38bd !important;
}

.text-primary {
  color: #3a38bd !important;
}

a {
  color: #3a38bd;
}

a:hover,
a:focus,
a:active {
  color: #6e92d4;
}
</style>



